<template>
	<div class = "box">
		<div>
		<ul>
			<li v-for=" (item,index) in datalist">
				<div>
				    <p style="text-align:right;">
						<img style = "width: 30px; height: 30px;" src = "/删除.png" @click.stop="todelete(item , index)"/>
					</p>
					<img src = "项目.png" />
				</div>
				<p style="background-color: white;">{{item}}</p>
			</li>
		</ul>
		</div>
		<!-- 删除 -->
		<mydelete v-show = "isShow" @isTrue = "handleDelete" @isFalse = "cancleDelete" :myname = "name"></mydelete>
		<!-- 删除完毕 -->
	</div>
</template>

<script>
import mydelete from './Delete.vue'
	export default{
		data(){
			return{
				current:0 ,
			isShow : false ,
			name :"12354",
			index : 0,
			datalist:[ '项目一：科研开发' , '项目二：***' , '项目三：***' , '项目四：***']
			}
		},
		methods:{
			todelete(item , index){
				// console.log('delete')
				this.name = item
				this.index = index
				this.isShow = true
				
			},
			handleDelete(){
				this.isShow = false
				// console.log('handleDelete')
				this.datalist.splice(this.index , 1)
				console.log("deletedeltet")
			},
			cancleDelete(){
				this.isShow = false
			}
		},
		components:{
			mydelete
		}
	}
</script>

